  margin: 36rpx 32rpx 44rpx;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}
.item-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icon {
  width: 98rpx;
  height: 98rpx;
}
.img {
  width: 100%;
  height: 100%;
  display: block;
}
.name {
  margin-top: 16rpx;
  font-size: 26rpx;
  color: #323233;
}
</style>
